.list {
  width: 400px;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

.item {
  margin-top: 12px;
}

.button {
  margin-right: 5px;
  margin-left: 5px;
  padding: 5px;
  border-radius: 10px;
}
.button:hover,
.button:focus {
  background-color: rgb(236, 161, 75);
}
